@mixin response-codes($color)
{
    background-color: rgba($color, 1.0);
    padding: 4px 10px;
    width: fit-content;
    border-radius: 4px;
    height: fit-content;
    margin-left: 10px;

    h1
    {
        color: white;
        font-size: 12px;
        font-weight: normal;
        margin: 0;
        padding: 0;
    }
}

.response-code
{
    @include text_body();

    &.informational
    {
        @include response-codes($_color-informational)
    }
    
    &.successful
    {
        @include response-codes($_color-successful)
    }

    &.redirects
    {
        @include response-codes($_color-redirects)
    }

    &.client-error
    {
        @include response-codes($_color-client-error)
    }

    &.server-error
    {
        @include response-codes($_color-server-error)
    }

    &.not-defined
    {
        @include response-codes($_color-not-defined)
    }
}

.responses-content-type
{
    display: flex;
    flex-direction: row;
    margin-left: auto;
    align-items: center;

    h1
    {
        font-size: small;
    }
    
}

.response-code
{
    &.row
    {
        margin-right: 0.5rem;
    }
}