.main
{
    background-color: $bg-darker;
    color: $white;
    padding: 0 3em;
    min-height: 100vh;
    width: 70vw;
    padding-bottom: 10rem;
}
@media(min-width: 100rem)
{
    .main
    {
        width: calc(100vw - 30rem);
    }
}

.mainlayout {
    display: flex;
    flex-direction: row;
}

.closed
{
    display: none;
}

.wrapper
{
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
}


.opblock-tag-section
{
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    padding: 1rem 0;
    &:not(:last-child)
    {
        border-bottom: 1px solid $white-gray;
    }

    &.is-open
    {
        min-height: calc(100vh - 105px);
    }
}

.opblock-tag
{
    display: flex;
    align-items: center;

    padding: .5rem 20px .5rem 0;

    transition: all .2s;
    .markdown p {
        margin: 0;
    }
}


@mixin method($color)
{
    .opblock-summary-method
    {
        border: $color 2px solid;
    }

    .tab-header .tab-item.active h4 span:after
    {
        background: $color;
    }

    &.method-shortcut
    {
        border: $color solid 1px;
    }

    &.method-shortcut:hover
    {
        background-color: lighten($main-background, $amount: 20);
    }
}


.opblock-tag
{
    font-size: 2rem;
    font-weight: 400;

    margin: 0 0 5px 0;

    &.no-desc
    {
        span
        {
            flex: 1;
        }
    }

    svg
    {
        transition: all .4s;
    }

    small
    {
        font-size: 14px;
        font-weight: normal;

        flex: 1;

        padding: 0 10px;

        @include text_body();
    }
}

.parameter-controls {
    margin-top: 0.75em;
}

.example-header
{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px;

    h1
    {
        font-size: smaller;
        font-weight: normal;
        padding-right: 10px;
    }
}

.examples {
    &__title {
        display: block;
        font-size: 1.1em;
        font-weight: bold;
        margin-bottom: 0.75em;
    }

    &__section {
        margin-top: 1.5em;
    }
    &__section-header {
        font-weight: bold;
        font-size: .9rem;
        margin-bottom: .5rem;
        // color: #555;
    }
}

.examples-select {
    margin-bottom: .75em;

    &__section-label {
        font-weight: bold;
        font-size: .9rem;
        margin-right: .5rem;
    }
}

.example {
    &__section {
        margin-top: 1.5em;
    }
    &__section-header {
        font-weight: bold;
        font-size: .9rem;
        margin-bottom: .5rem;
        // color: #555;
    }
}

.view-line-link
{
    position: relative;
    top: 3px;

    width: 20px;
    margin: 0 5px;

    cursor: pointer;
    transition: all .5s;
}



.opblock
{
    margin: 0 0 15px 0;
    padding: 1rem 0 0 0;
    border-radius: 6px;
    box-shadow: 0 0 3px rgba($opblock-box-shadow-color,.19);
    &-headergroup {
        display: flex;
        align-items: center;
        .opblock-tag {
            margin-bottom: 0;
        }
    }

    &.opblock-deprecated
    {
        > :not(.opblock-summary)
        {
            > * > * > * 
            {
                > *:not(.example-wrapper)
                {
                    opacity: 0.6;
                }
                
                > .example-wrapper
                {
                    > .example-header
                    {
                        opacity: 0.6;
                    }
                    > .highlight-code
                    {
                        > .highlight-code-inner
                        {
                            opacity: 0.6;
                        }
                        > .dialog-ux
                        {
                            > .modal-ux
                            {
                                background-color: #232438;
                                > *
                                {
                                    opacity: 0.6;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &.method-shortcut
    {
        padding: 6px 17.5px;
        margin-right: 10px;
        font-size: 17px;
        text-transform: uppercase;
        font-weight: 100;
        cursor: pointer;
    }

    .tab-header
    {
        display: flex;

        flex: 1;

        .tab-item
        {
            padding: 0 40px;

            cursor: pointer;

            &:first-of-type
            {
                padding: 0 40px 0 0;
            }
            &.active
            {
                h4
                {
                    span
                    {
                        position: relative;


                        &:after
                        {
                            position: absolute;
                            bottom: -15px;
                            left: 50%;

                            width: 120%;
                            height: 4px;

                            content: '';
                            transform: translateX(-50%);

                            background: $opblock-tab-header-tab-item-active-h4-span-after-background-color;
                        }
                    }
                }
            }
        }
    }

    &-section
    {
        &.server-response
        {
            border-radius: 5px;
        }
    }

    .opblock-section-header
    {
        display: flex;
        align-items: center;

        padding: 20px 3rem 10px;
        cursor: default;
        min-height: 50px;
        margin: 10px -3rem;

        &.close
        {
            cursor: pointer;
            
            &:hover
            {
                background-color: rgba($white, $alpha: 0.04);
            }
        }

        >label
        {
            font-size: 12px;
            font-weight: bold;

            display: flex;
            align-items: center;

            margin: 0;
            margin-left: auto;

            @include text_headline();

            >span
            {
                padding: 0 10px 0 0;
            }
        }

        h4
        {
            font-size: 1.125rem;

            flex: 1;

            margin: 0;

            @include text_headline();
        }

        >.icon
        {
            margin: 0;
            padding: 0 10px 0 0;
            background: transparent;
        }
    }

    .opblock-summary-method
    {
        font-size: 1.5rem;

        padding: .25rem 1rem;

        text-align: center;

        border-radius: 4px;
        text-shadow: 0 1px 0 rgba($opblock-summary-method-text-shadow-color,.1);
        cursor: default;

        @include text_headline($opblock-summary-method-font-color);
    }

    .opblock-summary-path,
    .opblock-summary-operation-id,
    .opblock-summary-path__deprecated
    {
        font-size: 1.75rem;
        @media (max-width: 768px) {
          font-size: 12px;
        }
        cursor: text;


        display: flex;
        align-items: center;

        word-break: break-word;

        padding: 0 10px;

        @include text_code();

        .color
        {
            color: #93f523;
            font-weight: 500;
        }

    }

    .opblock-summary-path__deprecated
    {
        text-decoration: line-through;
    }

    .opblock-summary-operation-id
    {
        font-size: 14px;
    }

    .opblock-summary-description
    {
        font-size: 1rem;
        line-height: 1.4;

        flex: 1 1 auto;

        word-break: break-word;

        @include text_body();
    }

    .opblock-summary
    {
        border: none;
        display: flex;
        align-items: center;
        width: calc(100% + 6rem);
        margin-left: -3rem;
        background: $bg-medium;
        padding: 2rem 03rem;
        position: sticky;
        top: 75px;
        z-index: 2;
        -moz-box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        -webkit-box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
        box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

        &.opblock-deprecated
        {
            background: #191a30;
            @include method($_color-disabled);

            > *
            {
                opacity: 0.6;
            }
        }
    
        .view-line-link
        {
            position: relative;
            top: 2px;

            width: 0;
            margin: 0;

            cursor: pointer;
            transition: all .5s;
        }

        &:hover
        {
            .view-line-link
            {
                width: 18px;
                margin: 0 5px;
            }
        }
    }



    &.opblock-post
    {
        @include method($_color-post);
    }

    &.opblock-put
    {
        @include method($_color-put);
    }

    &.opblock-delete
    {
        @include method($_color-delete);
    }

    &.opblock-get
    {
        @include method($_color-get);
    }

    &.opblock-patch
    {
        @include method($_color-patch);
    }

    &.opblock-head
    {
        @include method($_color-head);
    }

    &.opblock-options
    {
        @include method($_color-options);
    }

    .opblock-schemes
    {
        padding: 8px 20px;

        .schemes-title
        {
            padding: 0 10px 0 0;
        }
    }
}

.filter
{
    .operation-filter-input
    {
        width: 100%;
        margin: 20px 0;
        padding: 10px 10px;

        border: 2px solid $operational-filter-input-border-color;
    }
}

.model-example {
    margin-top: 1em;
}

.tab
{
    display: flex;

    padding: 0;

    list-style: none;

    li
    {
        font-size: 12px;

        min-width: 60px;
        padding: 0;

        cursor: pointer;

        @include text_headline();

        &:first-of-type
        {
            position: relative;

            padding-left: 0;
            padding-right: 12px;

            &:after
            {
                position: absolute;
                top: 0;
                right: 6px;

                width: 1px;
                height: 100%;

                content: '';

                background: rgba($tab-list-item-first-background-color,.2);
            }
        }

        &.active
        {
            font-weight: bold;
        }
    }
}

.opblock-description
{
    max-height: 250px;
    overflow: auto;

    scrollbar-color: darkgrey transparent !important;
    scrollbar-width: thin;

    &::-webkit-scrollbar
    {
        width: 0.5em;
        height: 0.5em;
        
        &-track
        {
            color: rgba(255, 255, 255, 0.5);
        }
        &-thumb
        {
            background-color: darkgrey;
            border-radius: 3px; 
        }
        &-corner
        {
            background-color: transparent;
        }
    }
}

.opblock-description-wrapper,
.opblock-external-docs-wrapper,
.opblock-title_normal
{
    font-size: 12px;

    margin: 0 0 5px 0;
    padding: 5px;

    @include text_body();

    h4
    {
        font-size: 1.125rem;

        margin: 0 0 5px 0;

        @include text_body();
    }

    p
    {
        font-size: 1.125rem;
        line-height: 1.4;
        margin: 0 0 1rem 0;

        @include text_body();
    }
}

.opblock-external-docs-wrapper {
  h4 {
    padding-left: 0px;
  }
}

.execute-wrapper
{
    padding: 20px;

    text-align: right;

    .btn
    {
        width: 100%;
        padding: 8px 40px;
    }
}
.body-param 
{
    &__text
    {
        margin: 10px;
        padding: 10px;
        font-size: large;
        border: rgba($schema-border,.5) 1px solid;
        box-shadow: 0px 0px 10px 2.5px rgba($white ,.1);
        max-width: -webkit-fill-available;
        background: $bg-lighter;
        width: 100%;

        scrollbar-width: thin;
        scrollbar-color: rgba($schema-border,.5) transparent !important;

        &:focus
        {
            outline: none;
            border: $schema-border-focus solid thin !important;
        }
        
        &::-webkit-scrollbar {
          width: 14px;
          height: 14px;
        }
        
        &::-webkit-scrollbar-track {
          box-shadow: inset 0 0 14px 14px transparent;
          border: solid 4px transparent;
        }
        
        &::-webkit-scrollbar-thumb {
          box-shadow: inset 0 0 14px 14px $edit-scroll;
          border: solid 4px transparent;
          border-radius: 14px;
        }
        
        /* set button(top and bottom of the scrollbar) */
        &::-webkit-scrollbar-button {
          display: none;
        }
        
        &::-webkit-scrollbar-corner {
            background-color: transparent;
        }
    }    

    &-options
    {
        display: flex;
        flex-direction: column;

        .body-param-edit
        {
            padding: 10px 0;
        }

        label
        {
            padding: 8px 0;
            select
            {
                margin: 3px 0 0 0;
            }
        }
    }

}
.example-wrapper {
    margin-bottom: 1rem;
}
.responses-inner
{
    padding: 20px 0;

    h5,
    h4
    {
        font-size: 12px;

        margin: 10px 0 5px 0;

        @include text_body();
    }
}

.response-col_links
{
    padding-left: 2em;
    max-width: 40em;
    font-size: 14px;

    @include text_body();

    .response-undocumented
    {
        font-size: 11px;

        @include text_code($response-col-links-font-color);
    }
}

.opblock-body
{
    padding-top: 2rem;
  .opblock-loading-animation
  {
    display: block;
    margin: 3em;
    margin-left: auto;
    margin-right: auto;
  }
}

.opblock-body pre.microlight
{
    
    font-size: 12px;

    margin: 0;
    padding: 10px;

    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-all;
    word-break: break-word;
    hyphens: auto;

    border-radius: 4px;
    background: $opblock-body-background-color;

    overflow-wrap: break-word;
    @include text_code($opblock-body-font-color);
    span
    {
        color: $opblock-body-font-color !important;
    }

    .headerline
    {
        display: block;
    }
}

.highlight-code {
  position: relative;
  background: $bg-lighter;
  color: #cccccc;
  margin: 0;
  padding: 30px 6.5px 15px 15px;
  border-radius: 7.5px;
  

  > .microlight {
    overflow-y: auto;
    max-height: 400px;
    min-height: 6em;
  }
}

.highlight-button {
    
    color: white;
    margin: 3px;
    padding: 4px;
    border-radius: 3.5px;
    background-color: $transparent;
    border-radius: 4px;

    &-wrapper
    {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        flex-direction: row;
        margin: 0;
        align-items: center;
        margin-right: 10px;
    }

    &:hover
    {
        background-color: rgba($color: white, $alpha: 0.1);
    }
}

.highlight-hover 
{
    color: rgba($color: white, $alpha: 0.2);
    font-size: small;
    background-color: transparent;
    font-family: Calibri, sans-serif;
    padding-right: 5px;
    margin: 0;
    cursor: default;
}

.bottom {
    position: absolute;
    bottom: 0;   
}


.download-contents {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
  background: #7d8293;
  text-align: center;
  padding: 5px;
  border-radius: 4px;
  font-family: sans-serif;
  font-weight: 600;
  color: white;
  font-size: 14px;
  height: 30px;
  width: 75px;
}
.schemes
{
    margin: 0 10px 0 0;
    background: $scheme-background-color;
}

.loading-container
{
    padding: 40px 0 60px;
    margin-top: 1em;
    min-height: 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .loading
    {
        position: relative;


        &:after
        {
            font-size: 11px;

            position: absolute;
            top: 50%;
            left: 50%;

            content: 'loading';
            transform: translate(-50%,-50%);
            text-transform: uppercase;

            @include text_headline();
            color: $loading-container-before-border-top-color;
        }

        &:before
        {
            position: absolute;
            top: 50%;
            left: 50%;

            display: block;

            width: 60px;
            height: 60px;
            margin: -30px -30px;

            content: '';
            animation: rotation 1s infinite linear, opacity .5s;

            opacity: 1;
            border: 2px solid rgba($loading-container-before-border-color, .5);
            border-top-color: $loading-container-before-border-top-color;
            border-radius: 100%;

            backface-visibility: hidden;

            @keyframes rotation
            {
                to
                {
                    transform: rotate(360deg);
                }
            }
        }
    }
    .loading-small
    {
        position: relative;
        &:before
        {
            position: absolute;
            top: 50%;
            left: 50%;

            display: block;

            width: 40px;
            height: 40px;
            margin: -20px -20px;

            content: '';
            animation: rotation 1s infinite linear, opacity .5s;

            opacity: 1;
            border: 2px solid rgba($loading-container-before-border-color, .5);
            border-top-color: $loading-container-before-border-top-color;
            border-radius: 100%;

            backface-visibility: hidden;

            @keyframes rotation
            {
                to
                {
                    transform: rotate(360deg);
                }
            }
        }
    }
}
.response-controls {
    padding-top: 1em;
    display: flex;
}

.response-control-media-type {
    margin-right: 1em;

    &--accept-controller {
        select {
            border-color: $response-content-type-controls-accept-header-select-border-color;
        }
    }

    &__accept-message {
        color: $response-content-type-controls-accept-header-small-font-color;
        font-size: .7em;
    }

    &__title {
        display: block;
        margin-bottom: 0.2em;
        font-size: .7em;
    }
}

.response-control-examples {
    &__title {
        display: block;
        margin-bottom: 0.2em;
        font-size: .7em;
    }
}

@keyframes blinker
{
    50%
    {
        opacity: 0;
    }
}


section
{
    h3
    {
        @include text_headline();
    }
}

a.nostyle {
  text-decoration: inherit;
  color: inherit;
  display: inline;

  &:visited {
    text-decoration: inherit;
    color: inherit;
    cursor: pointer;
  }
}

.version-pragma {
  height: 100%;
  padding: 5em 0px;

  &__message {
    display: flex;
    justify-content: center;
    height: 100%;
    font-size: 1.2em;
    text-align: center;
    line-height: 1.5em;

    padding: 0px .6em;

    > div {
      max-width: 55ch;
      flex: 1;
    }

    code {
      background-color: #dedede;
      padding: 4px 4px 2px;
      white-space: pre;
    }
  }
}

.try-out_inner 
{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.text-unauthorized 
{
    font-size: .875rem;
    color: $try-button-unauth-background;
    padding-left: 6.5px;
}

.type-chooser
{
    display: flex;
    flex-direction: row;
    align-items: center;

    &.response
    {
        margin-left: auto;
    }

    h1
    {
        font-size: .875rem;
        font-weight: 100;
        margin-right: 10px;
    }
}

.json-schema
{
    width: -webkit-fill-available;

    input[type=text],
    input[type=password],
    input[type=search],
    input[type=email],
    input[type=file],
    textarea
    {
        background-color: $bg-lighter !important;
        color: $schema-text !important;
        border: rgba($schema-border,.19) solid 2px !important;
        margin: 0;
        @include text_body();

        &:focus
        {
            outline: none;
            border: $schema-border-focus solid thin !important;
        }
    }

    &.required
    {
        input[type=text],
        input[type=password],
        input[type=search],
        input[type=email],
        input[type=file],
        textarea
        {
            &::placeholder
            {
                color: $schema-text-required !important;
                
                &:after
                {
                    content: '*';    
                    color: $schema-text-required !important;    
                }
            }
        }
    }
}