.markdown, .renderedMarkdown {
  p, pre, ul {
    margin: 0;
  }
  pre {
    color: $alto-gray;
    font-weight: normal;
    white-space: pre-wrap;
    background: none;
    padding: 0px;
  }

  code {
    font-size: 14px;
    padding: 5px 7px;

    border-radius: 4px;
    background: rgba($info-code-background-color,.05);

    @include text_code($info-code-font-color);
  }

  pre > code {
    display: block;
  }
}