.btn
{
    font-size: 1rem;
    @include text_headline();
    font-weight: 700;
    letter-spacing: 0.05em;

    padding: .5rem .75rem;
    margin: 5px;

    min-width: 5em;

    transition: all .3s;

    border: 2px solid $btn-border-color;
    border-radius: 5px;
    background: transparent;
    box-shadow: 0 1px 2px rgba($btn-box-shadow-color,.1);

    &:first-child {
        margin-left: 0px;
    }

    &.btn-sm
    {
        font-size: 12px;
        padding: 4px 23px;
    }

    &[disabled]
    {
        cursor: not-allowed;

        opacity: .3;
    }

    &:hover
    {
        box-shadow: 0 0 5px rgba($btn-box-shadow-color,.3);
        -webkit-filter: brightness(80%);
    }

    &.cancel
    {
        border: none;
        background: none;
        font-size: 1rem;
    }

    &.try-out__btn
    {
        background: $try-button-background;
        border-color: $try-button-background;
    }

    &.unauthorized
    {
        background: $try-button-unauth-background;
        color: $try-button-unauth-text;
        border-color: $try-button-unauth-background;
    }

    &.error
    {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    }

    &.authorize
    {
        margin: 0;
        color: $btn-authorize-font-color;
        border-color: $btn-authorize-border-color;
        background-color: $btn-authorize-background-color;
        text-align: center;
        margin-left: 0;

        span
        {
            float: left;

            padding: 4px 20px 0 0;
        }

        svg
        {
            fill: $btn-authorize-svg-fill-color;
        }
    }

    &.execute
    {
        background-color: $btn-execute-background-color-alt;
        color: $btn-execute-font-color;
        border-color: $btn-execute-border-color;
        font-weight: normal;

    }

    &-feedback
    {
        width: fit-content;
        font-weight: 100;
        text-decoration: none;
        &-wrapper
        {
            flex: 1;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }
    }
}

.btn-group
{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 1rem 0;
}

.authorization__btn
{
    padding: 0 10px;

    border: none;
    background: none;

    &.locked
    {
        opacity: 1;
    }

    &.unlocked
    {
        opacity: .4;
    }
}

.expand-methods,
.expand-operation
{
    border: none;
    background: none;

    svg
    {
        width: 20px;
        height: 20px;
    }
}

.expand-methods
{
    padding: 0 10px;

    &:hover
    {
        svg
        {
            fill: $expand-methods-svg-fill-color-hover;
        }
    }

    svg
    {
        transition: all .3s;

        fill: $expand-methods-svg-fill-color;
    }
}


button
{
    cursor: pointer;
    outline: none;

    &.invalid
    {
        @include invalidFormElement();
    }
}
