table
{
    width: 100%;
    padding: 0 10px;

    border-collapse: collapse;

    &.model
    {
        tbody
        {
            tr
            {
                td
                {
                    padding: 0;

                    vertical-align: top;

                    &:first-of-type
                    {
                        width: 174px;
                        padding: 0 0 0 2em;
                    }
                }
            }
        }
    }

    &.headers
    {
        td
        {
            font-size: 1rem;
            font-weight: 400;

            vertical-align: middle;

            @include text_code();
        }
    }

    tbody
    {
        tr
        {
            td
            {
                padding: 10px 5px 0;
                vertical-align: top;
                font-size: 1rem;
            }
        }
    }

    thead
    {
        tr
        {
            th,
            td
            {
                font-weight: 600;

                padding: 0 5px 10px;

                text-align: left;

                border-bottom: 1px solid $border-reduced-white;

                @include text_body();
                font-size: 1rem;
            }
        }
    }
}

.parameter__name
{
    font-size: 1rem;
    font-weight: 100;
    padding: 0px;
    //border: thin solid $main-background;

    @include text_body();

    &.required
    {
        color: $table-parameter-name-required-font-color;

        &:after
        {
            font-size: 14px;
            content: '*';
            color: $table-parameter-name-required-font-color;
        }
    }
}

.parameter__description,
.parameter__type,
.parameter__constraints,
.parameter__example
{
    padding: 0;
    max-height: 150px;
    overflow: auto;

    scrollbar-color: darkgrey transparent !important;
    scrollbar-width: thin;

    &::-webkit-scrollbar
    {
        width: 0.25em;
        height: 0.25em;
        
        &-track
        {
            color: rgba(255, 255, 255, 0.5);
        }
        &-thumb
        {
            background-color: darkgrey;
            border-radius: 3px; 
        }
        &-corner
        {
            background-color: transparent;
        }
    }
    //border: thin solid $main-background;
}

.parameter__in,
.parameter__extension
{
    font-size: 12px;
    font-style: italic;
    padding: 8px;

    @include text_code($table-parameter-in-font-color);
}

.parameter__deprecated
{
    font-size: 12px;
    font-style: italic;
    padding: 8px;

    @include text_code($table-parameter-deprecated-font-color);
}

.parameter__empty_value_toggle {
    font-size: 13px;
    padding: 8px;

    input {
        margin-right: 7px;
    }

    &.disabled {
        opacity: 0.7;
    }
}


.table-container
{
    margin: 1rem 0 2rem 0;
}
