@mixin nav-method($color)
{
    background-color: rgba($color, 0.65);

    &.selected 
    {
        background-color: rgba($color, 1.0);    
    }
}

.navigation
{
    &-wrapper
    {
        position: sticky;
        top: 75px;
        width: 30vw;
        max-width: 30rem;
        height: calc(100vh - 75px);
        overflow: auto;
        background-color: $bg-light;
        padding: 1em;

        scrollbar-color: $navigation-scroll transparent !important;
        scrollbar-width: thin;
    
        &::-webkit-scrollbar {
            width: 20px;
            height: 20px;
        }
        
        &::-webkit-scrollbar-track {
            box-shadow: inset 0 0 20px 20px transparent;
            border: solid 6px transparent;
        }
        
        &::-webkit-scrollbar-thumb {
            box-shadow: inset 0 0 20px 20px $navigation-scroll;
            border: solid 6px transparent;
            border-radius: 20px;
        }
        /* set button(top and bottom of the scrollbar) */
        &::-webkit-scrollbar-button {
            display: none;
        }
    
        &::-webkit-scrollbar-corner {
            background-color: transparent;
        }
    }

    &-section
    {
        padding-top: 0.5em;
        padding-bottom: 1em;
        color: $white;
        cursor: default;
    
        >.header
        {
            display: flex;
            flex-direction: row;
            align-items: center;
        }
        >.header.not-supported-auth
        {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &-headline {
        font-size: medium;
        text-transform: uppercase;
    }

    &-sub-headline {
        font-size: medium;
        padding: 0;
        margin: 0;
        margin-top: -10px;
        font-weight: 100;
    }

    &-section:not(:last-child)
    {
        border-bottom: 1px solid $bg-light-blue;
    }

    &-link
    {

        &.tag
        {
            &.header
            {
                padding: 0.2rem 0.2rem 0.2rem 1rem;
                margin: 0 -1rem;

                &:hover
                {
                    background-color: rgba($white, 0.05);
                }
                &.open
                {
                    h1{
                        font-weight: 600;
                    }
                }
            }

            &.body
            {
                border-left: solid thin $bg-light-blue;
                // margin-left: 1rem;
                margin-right: -1rem;
            }

            &.outer {
                margin-bottom: 1rem;
            }
        }

        &.method
        {
            display: flex;
            flex-direction: row;
            align-items: center;
            width: 100%;
            padding: .375rem 1rem;
            &:hover
            {
                background-color: rgba($white, 0.05);
            }

            h1
            {
                font-size: .875rem;
                font-weight: normal;
            }

            &.selected
            {
                h1
                {
                    font-weight: bold;
                }
            }

            
            >.deprecated
            {
                font-style: italic;
                font-size: xx-small;
                margin-left: 4px;
            }

            > .method-icon
            {
                margin-left: auto;
                border-radius: 4px;
                padding: .25rem .375rem;
                padding-top: 0;
                padding-bottom: 0;
                margin-top: 0;
                margin-bottom: 0;
                transition: all .25s ease-out;
                h1{
                    font-size: .675rem;
                    color: white;
                    font-weight: bold;
                }   

                &.post
                {
                    @include nav-method($_color-post)
                }
            
                &.put
                {
                    @include nav-method($_color-put);
                }
            
                &.delete
                {
                    @include nav-method($_color-delete);
                }
            
                &.get
                {
                    @include nav-method($_color-get);
                }
            
                &.patch
                {
                    @include nav-method($_color-patch);
                }
            
                &.head
                {
                    @include nav-method($_color-head);
                }
            
                &.options
                {
                    @include nav-method($_color-options);
                }
            
                &.deprecated
                {                
                    @include nav-method($_color-disabled);
                }
     
            }
        }
        &tag {
            display: block;
            font-size: 1.25rem;
            font-weight: normal;
            padding: .25rem .5rem .25rem 0;
            &--sub {
                font-size: 1.125rem;
                padding: .25rem .5rem .25rem 0;
                &.chip {
                    font-size: .875rem;
                    display: inline-block;
                    padding: .25rem 0;
                }
            }
        }
    }
}

.auth-item
{
    color: $navigation-auth-item;
    background: $bg-light;
    margin: 0 10px 0 0;
}