.live-response
{
    background-color: $transparent;
    &.header
    {
        display: flex;
        flex-direction: row;
        align-items: center;
        background-color: $transparent;
        border-color: 2px gray solid;
        padding: .25rem;
        margin-bottom: 10px;
        cursor: default;

        &.close
        {
            cursor: pointer;
            &:hover
            {
                background-color: rgba($white, $alpha: 0.03);
            }
        }

        >h1
        {
            font-weight: normal;
            font-size: 15px;
            margin-left: 0.5em;
            padding-right: 0.5m;
        }
    }
    &.body {
        margin: 0 .75rem;
        font-weight: bold;
    }
    & > span {
        display: block;
        padding: .5rem;
        color: $_color-client-error;
    }
    &.wrapper {
        padding: 0 0 15px 0;
        display: flex;
        flex-direction: column;
        border-radius: 5px;
        background-color: rgba($white, $alpha: 0.05);
        max-width: 100%;
    }
}