.model
{
    font-size: 12px;
    font-weight: 300;
    

    @include text_code();

    &-loading-animation
    {
      display: block;
      margin: 3em;
      margin-left: auto;
      margin-right: auto;
    }

    &.object
    {
        border-radius: 6px;
        padding: 5px;
        width: fit-content;
    }

    .deprecated
    {
        span,
        td
        {
            color: $model-deprecated-font-color !important;
        }

        > td:first-of-type {
            text-decoration: line-through;
        }
    }
    &-toggle
    {
        font-size: 10px;

        position: relative;
        top: 6px;

        display: inline-block;

        margin: auto .3em;

        cursor: pointer;
        transition: transform .15s ease-in;
        transform: rotate(90deg);
        transform-origin: 50% 50%;

        &.collapsed
        {
            transform: rotate(0deg);
        }

        &:after
        {
            display: block;

            width: 20px;
            height: 20px;

            content: '';

            background: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2224%22%20height%3D%2224%22%20viewBox%3D%220%200%2024%2024%22%3E%0A%20%20%20%20%3Cpath%20d%3D%22M10%206L8.59%207.41%2013.17%2012l-4.58%204.59L10%2018l6-6z%22/%3E%0A%3C/svg%3E%0A) center center no-repeat;
            background-size: 100%;
        }
    }

    &-jump-to-path
    {
        position: relative;

        cursor: pointer;

        .view-line-link
        {
            position: absolute;
            top: -.4em;

            cursor: pointer;
        }
    }

    &-title
    {
        position: relative;

        &:hover .model-hint
        {
            visibility: visible;
        }
    }

    &-hint
    {
        position: absolute;
        top: -1.8em;

        visibility: hidden;

        padding: .1em .5em;

        white-space: nowrap;

        color: $model-hint-font-color;
        border-radius: 4px;
        background: rgba($model-hint-background-color,.7);
    }

    p
    {
        margin: 0 0 1em 0;
    }

    &-header
    {
        display: flex;
        align-items: center;

        padding: 20px 0 10px 0;
        
        min-height: 50px;

        cursor: pointer;

        >.icon
        {
            margin: 0;
            padding: 0 10px 0 0;
        }

        h1
        {
            font-size: 1.125rem;
            flex: 1;
            margin: 0;
            @include text_headline();
            font-weight: 100;
        }
    }
}


.models
{

    &.header
    {
        display: flex;
        align-items: center;
        width: calc(100% + 6rem);
        margin-left: -3rem;
        background: $bg-medium;
        padding: 2rem 03rem;
        position: sticky;
        top: 75px;
        z-index: 20;
        box-shadow: 6px 3px 10px rgba(0,0,0,.25);
    }
    h4
    {
        font-size: 16px;

        display: flex;
        align-items: center;

        margin: 0;
        padding: 10px 20px 10px 10px;

        cursor: pointer;
        transition: all .2s;

        @include text_headline($section-models-h4-font-color);

        svg
        {
            transition: all .4s;
        }

        span
        {
            flex: 1;
        }

        &:hover
        {
            background: rgba($section-models-h4-background-color-hover,.02);
        }
    }

    h5
    {
        font-size: 16px;

        margin: 0 0 10px 0;

        @include text_headline($section-models-h5-font-color);
    }

    .model-jump-to-path
    {
        position: relative;
        top: 5px;
    }

    .model-container
    {
        margin: 0 20px 15px;
        position: relative;
        width: 100%;

        transition: all .5s;

        border-radius: 4px;


        &:first-of-type
        {
            margin: 20px;
        }

        &:last-of-type
        {
            margin: 0 20px;
        }

        .models-jump-to-path {
          position: absolute;
          top: 8px;
          right: 5px;
          opacity: 0.65;
        }
    }
    &.body
    {
        min-height: calc(100vh - 105px);
    }
}

.inner-object
{
    width: fit-content
}


.model-wrapper
{
    padding: 10px;
    display: inline-block;
    overflow: auto;
    max-height: 50vh;
    width: 100%;

    border-radius: 4px;
    background: $model-object-background-color;

    scrollbar-color: $model-box-scroll transparent !important;
    scrollbar-width: thin;

    &::-webkit-scrollbar {
        width: 20px;
        height: 20px;
    }
    
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 20px 20px transparent;
        border: solid 6px transparent;
    }
    
    &::-webkit-scrollbar-thumb {
        box-shadow: inset 0 0 20px 20px $model-box-scroll;
        border: solid 6px transparent;
        border-radius: 20px;
    }

    /* set button(top and bottom of the scrollbar) */
    &::-webkit-scrollbar-button {
        display: none;
    }

    &::-webkit-scrollbar-corner {
        background-color: transparent;
    }

    .model-jump-to-path
    {
        position: relative;
        top: 4px;
    }

    &.deprecated
    {
        opacity: .5;
    }
}

.model-text
{
    font-size: 1rem;
    margin: 0;
    font-weight: 100;
}


.model-title
{
    font-size: 16px;

    @include text_headline($section-models-model-title-font-color);
    text-transform: none !important;
}

.model-deprecated-warning
{
    font-size: 16px;
    font-weight: 600;

    margin-right: 1em;

    @include text_headline($_color-delete);
}


span
{
     > span.model
    {
        .brace-close
        {
            padding: 0 0 0 10px;
        }
    }
}

.prop-name
{
    display: inline-block;

    margin-right: 1em;
}

.prop-type
{
    color: $prop-type-font-color;
}

.prop-enum
{
    display: block;
}
.prop-format
{
    color: $prop-format-font-color;
}
