.auth 
{
    &-additional 
    {
        display: flex;
        flex-direction: row;
        align-items: center;    
        justify-content: space-between;
        
        >ul {
            margin: 0;
        }
    }
    &-simple
    {
        display: flex;
        flex-direction: row;
        align-items: center;    
        justify-content: space-between;
        .schemes-title 
        {
            display:none;
        }
        label {
            margin-right: .5rem;
            margin-top: .25rem;
        }
        
        input, label
        {
            width: 100%;
            background-color: $bg-light-blue;
            border: thin solid $auth-input-border;
            color: $auth-input-text;

            &:focus
            {
                outline: none;
                border: thin solid $form-focus-border;
            }

            &::placeholder 
            {
                color: $auth-input-placeholder;
            }
        }

        .basic
        {
            width: 100%;
        }
    
    }
}

.auth-input
{
    &.error
    {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
        
        &::placeholder
        {
            color: $auth-input-placeholder-error !important;
        }
    }
    &.encode
    {
        &:not(:focus)
        {
            -webkit-text-security: disc;
        }
    }
}

.auth-btn-wrapper
{
    padding: 20px 0;

    .btn-done {
      margin-right: 1em;
    }
}

.auth-wrapper
{
    display: flex;
    flex-direction: column;

    flex: 1;
    justify-content: flex-end;

    .authorize
    {
        margin-right: 10px;
    }
}

.auth-container
{
    margin: 0 0 10px 0;

    &:last-of-type
    {
        margin: 0;
        padding: 10px 20px;

        border: 0;
    }

    h4
    {
        margin: 5px 0 15px 0 !important;
    }

    .wrapper
    {
        margin: 0;
        padding: 0;
    }

    .errors
    {
        font-size: 12px;

        padding: 10px;

        border-radius: 4px;

        @include text_code();
    }
}

.scopes
{
    h2
    {
        font-size: 14px;

        @include text_headline();
    }
}

.scope-def
{
    padding: 0 0 20px 0;
    font-size: smaller;
}

.no-auth
{
    h1
    {
        font-size: larger;
        font-weight: 550;
    }
}